@font-face {
  font-family: "Vontens";
  font-style: normal;
  src: local("Vontens"), url(./fonts/Vontens.otf) format("opentype");
}

@font-face {
  font-family: "Adam";
  font-style: normal;
  src: local("Adam"), url(./fonts/Adam.otf) format("opentype");
}


html {
  font-size: 10px;
}

body {
  font-size: 2rem;
  display: grid;
  padding-bottom: 4rem;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  
  background-image: url("./imgs/topography.svg"), linear-gradient(110deg, #f93df6, #1d47d9);
  background-size: 340px, auto;
  min-height: calc(100vh - 100px);
  /* background: white; */
  background-attachment: fixed;
}
  
pre {
  font-size: 2rem;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  white-space: pre-wrap;
  -moz-tab-size: 3;
  tab-size: 3;
}

code {
  font-size: 1.8rem;
  font-family: Inconsolata, Monaco, Consolas, "Courier New", Courier, monospace;
}

header {
  background: url("./imgs/second-batch/sahil.jpg");
  background-size: cover;
  padding: 13rem 0rem 36rem 0rem;
  background-position: 0px 0rem;
  text-align: center;
  button {
    cursor: pointer;
    font-size: 2rem;
    padding: 2rem 3rem;
    border: none;
    font-weight: bold;
    font-family: Adam;
    background-color: white;
    color: #111;
    box-shadow: 2px 2px 3px 1px rgba(0,0,0,.1);
    border-radius: .2rem;
    display: block;
    margin: 0 auto 2rem auto;
  }
  input {
    border-radius: .5rem;
    padding: 2rem;
    border: none;
    min-width: 40rem;
    font-size: 2rem;
    font-family: Roboto, Helvetica, Arial, sans-serif;
  }
  .banner-text-container {
    .banner-text {
      font-family: Vontens, "Manoyri-Regular", "Roboto", "sans-serif";
      font-weight: 500;
      text-align: center;
      font-size: 11rem;
      color: white;
      margin: 0rem;
    }
    .banner-text--larger {
      font-size: 15rem;
      margin-top: -7rem;
      letter-spacing: 0.6rem;
    }
  }

}

main {
  padding: 0rem 4rem;
}

.dashbord__question-type {
  margin-bottom: 4rem;
  font-family: Adam;
}

.hidden {
  display: none;
}
